@charset "UTF-8";
.Settings {
  width: 100%;
  height: 100%;
}
.Settings * {
  box-sizing: border-box;
  font-size: 2.5vw;
  padding: 0;
  margin: 0;
}
.Settings .checkBox {
  width: 2.5vw;
  height: 2.5vw;
}
.Settings .radioButton {
  width: 2.5vw;
  height: 2.5vw;
}

.SettingHeader {
  display: flex;
  width: 100%;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  border-bottom: none;
}
.SettingHeader a {
  border-bottom: 10px solid #000;
  width: 20%;
  height: 100px;
  /* 추가: 텍스트 중앙 정렬을 위해 */
  display: flex;
  /* 추가: Flexbox 모델 적용 */
  justify-content: center;
  /* 추가: 내용물을 가운데 정렬 */
  align-items: center;
  /* 추가: 내용물을 세로 중앙 정렬 */
  text-decoration: none;
  /* 추가: 링크 밑줄 제거 */
}
.SettingHeader a:hover {
  background-color: #E1E1E1;
}

.SettingBranch .SettingBranchSelect .seletedBranch label {
  display: flex;
}
.SettingBranch .SettingBranchSelect .seletedBranch label input {
  width: 5vw;
  height: 5vw;
}
.SettingBranch .SettingBranchSelect .seletedBranch label h2 {
  width: 20%;
}
.SettingBranch .SettingBranchSelect .seletedBranch label:hover {
  cursor: pointer;
}
.SettingBranch .SettingBranchMenuAdd .branchCheck label {
  display: flex;
}
.SettingBranch .SettingBranchMenuAdd .branchCheck label input {
  width: 5vw;
  height: 5vw;
}
.SettingBranch .SettingBranchMenuAdd .branchCheck label:hover {
  cursor: pointer;
}

.SettingOption {
  width: 100%;
}
.SettingOption .SettingOptionCreate,
.SettingOption .SettingOptionList {
  width: 100%;
}
.SettingOption .SettingOptionCreate .optionInfo td:nth-child(1) {
  width: 20vw;
  text-align: center;
  border: 1px solid #000;
}
.SettingOption .SettingOptionCreate .optionInfo td:nth-child(2) {
  width: 80vw;
  text-align: left;
  border: 1px solid #000;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.SettingOption .SettingOptionCreate .optionInfo td:nth-child(2) div {
  width: 100%;
}
.SettingOption .SettingOptionCreate .optionInfo td:nth-child(2) input {
  width: 100%;
}
.SettingOption .SettingOptionCreate .optionInfo td:nth-child(2) .button {
  display: flex;
  justify-content: space-between;
}
.SettingOption .SettingOptionCreate .optionInfo .detailItem div {
  width: 100%;
  padding-left: 5vw;
}
.SettingOption .SettingOptionCreate .optionInfo .detailItem div input {
  width: 50%;
  border: 1px solid #000;
}
.SettingOption .SettingOptionList .basicOption .title h1 {
  width: 100%;
  padding: 4px;
  background-color: #ccc;
  padding: 4px;
}
.SettingOption .SettingOptionList .basicOption .name {
  width: 100%;
  display: grid;
  align-items: stretch;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
}
.SettingOption .SettingOptionList .basicOption .name div {
  border: 1px solid #000;
}
.SettingOption .SettingOptionList .basicOption .option .option_name {
  display: flex;
  width: 100%;
}
.SettingOption .SettingOptionList .basicOption .option .option_item_category {
  width: 100%;
  text-align: center;
  display: flex;
}
.SettingOption .SettingOptionList .basicOption .option .items {
  width: 100%;
  display: grid;
  align-items: stretch;
  grid-template-columns: repeat(4, 1fr);
}
.SettingOption .SettingOptionList .basicOption .option .items input {
  width: 100%;
  height: 100%;
}
.SettingOption .SettingOptionList .basicOption .option .items div {
  display: flex;
  justify-content: center;
  border: 1px solid #000;
}
.SettingOption .SettingOptionList .basicOption .option .items div .detail {
  display: block;
  width: 100%;
  border: none;
  border: 1px solid #000;
}
.SettingOption .SettingOptionList .basicOption .option .items .button {
  display: flex;
  justify-content: space-around;
}

.SettingProductCreate .productSettingLine {
  display: flex;
  align-items: stretch;
}
.SettingProductCreate .productSettingLine div {
  border: 1px solid #000;
}
.SettingProductCreate .productSettingLine .productSettingName {
  width: 100%;
}
.SettingProductCreate .productSettingLine .productSettingValue {
  width: 80%;
}
.SettingProductCreate label {
  cursor: pointer;
}
.SettingProductCreate .imageLabel {
  padding-top: 12px;
  padding-bottom: 12px;
}
.SettingProductCreate .imageLabel label {
  padding: 4px;
  background-color: #E1E1E1;
  border: 1px solid #000;
}

.SettingProductCreateOption {
  width: 100%;
}
.SettingProductCreateOption .optionHeader h2 {
  display: flex;
  align-items: center;
  padding-left: 12px;
  font-size: 24px;
  background-color: #ccc;
  min-height: 50px;
}
.SettingProductCreateOption .optionHeader .optionHeaders {
  display: flex;
}
.SettingProductCreateOption .optionHeader .optionHeaders span {
  width: 25%;
  text-align: center;
  border: 1px solid #000;
}
.SettingProductCreateOption .optionItem {
  display: flex;
}
.SettingProductCreateOption .optionItem span {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #000;
  width: 25%;
  height: 4vh;
}

