.Kiosk {
    width: 1080px;
    height: 1920px;
    position: relative;
    /* Ensure this is set */
}

// ====================================================================

.TopBar {
    width: 100%;
    height: 3%;
    top: 0;
    bottom: 0;

    .topButton {
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .branchSetting {
            width: 10%;
            height: 100%;
            border: 1px solid #000;
            background-color: rgba(#fff, 0);
        }

        .AIButton {
            width: 10%;
            height: 100%;
            border: 1px solid #000;
        }
    }

    .WebSocketVoiceStreaWming {
        width: 100%;
        height: 60px;
        border: 1px solid #000;
        background-color: red;
    }
}

.Category {
    width: 100%;
    height: 15%;
    // border: 1px solid #000;

    .menuCategory {
        width: 100%;
        height: 75%;
        display: flex;
        flex-wrap: wrap;

        .categoryBox {
            min-height: 40%;
            width: 20%;
            height: 50%;
            border-radius: 32px;
            margin: 4px;
            width: calc(20% - 8px);
            height: calc(50% - 8px);
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 36px;
            font-weight: bold;
            border: 1px solid #e1e1e1;
            // border: 1px solid #000;
        } 
        
    }

    .selectedCategory {
        background-color: #313131;
        color: #fff;
    }

    .topLine {
        width: 100%;
        height: calc(100% - 75%);
        background-color: #313131;

    }
}

.MenuList {
    width: 100%;
    height: 75%;
    display: flex;
    flex: wrap;
    align-items: flex-start;
    border-bottom: 1px solid #000;

    .menuListBox {
        position: relative;
        width: 25%;
        // border: 1px solid #000;

        .menuImage {
            width: 100%;

            img {
                width: 100%;
                height: 240px;
                object-fit: cover;
                padding: 5%;
                border-radius: 48px;
            }
        }

        .menuInfo {
            width: 100%;
            text-align: center;
            // border: 1px solid #000;

            .menuName {
                font-size: 2.5vw;
                font-weight: 400;
            }

            .menuPrice {
                padding-top: 10px;
                font-size: 2vw;
                font-weight: 400;
            }
        }
    }

    .soldout::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255, 255, 255, 0.7);
        /* 반투명한 흰색 */
        z-index: 2;
        /* 내용 위에 오도록 설정 */
    }
}

.OptionSection {
    color: #313131;
    position: absolute;
    top: 0;
    left: 0;
    width: 1080px;
    height: 1920px;
    background-color: rgba(#000, 0.6);
    margin: 0 auto;
    z-index: 3;

    .optionBox {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 800px;
        height: 1280px;
        padding-left: 50px;
        padding-right: 50px;
        background-color: #fff;
        border-radius: 12px;
        overflow: hidden;

        .PersonalOption {
            width: 100%;
            height: 100%;
            
            .personalTitle {
                // 110px
                margin-top: 50px;
                width: 100%;
                height: 60px;
                display: flex;
                font-size: 40px;
                color: #3F82C2;
                // border-bottom: 3px solid #000;

                div {
                    z-index: 200;
                    position: absolute;
                    top: 10px;
                    right: 15px;
                    width: 60px;
                    height: 60px;
                    // border: 1px solid #000;
                    border-radius: 64px;
                    background-color: #ACACAC;

                    span {
                        position: absolute;
                        width: 50px;
                        height: 8px;
                        top: 50%;
                        left: 50%;
                        background-color: #fff;
                        border-radius: 32px;

                    }

                    span:nth-child(1) {
                        transform: translate(-50%, -50%) rotate(45deg);
                    }

                    span:nth-child(2) {
                        transform: translate(-50%, -50%) rotate(-45deg);
                    }
                }
            }

            .personalOptions {
                padding-top: 20px;
                padding-left: 40px;

                .personalOptionItems {
      
                    h1 {
                        width: 100%;
                        font-size: 32px;
                    }
                    .personalOptionItem{
                        margin-top: 10px;
                        display: flex;
                        .items{
                            div{
                                display: flex;
                                justify-content: center;
                                text-align: center;
                                font-size: 24px;
                                font-weight: bold;
                                background-color: #e7e7e7e7;
                                width: 125px;
                                height: 125px;
                                border-radius: 12px;
                                margin-right: 20px;
                            }
                            .pick{
                                background-color: #3F82C2;
                                color: #fff;
                            }
                        }
                    }


                }


            }
        }

        .TOP {
            width: 100%;
            height: 330px;
            // border: 1px solid #000;

            .exitButton {
                // position: relative;
                width: 100%;
                height: 80px;
                right: 0;

                // display: flex;
                // align-items: center;
                // border: 1px solid #000;
                div {
                    z-index: 100;
                    position: absolute;
                    top: 10px;
                    right: 15px;
                    width: 60px;
                    height: 60px;
                    border-radius: 64px;
                    background-color: #ACACAC;

                    span {
                        position: absolute;
                        width: 50px;
                        height: 8px;
                        top: 50%;
                        left: 50%;
                        background-color: #fff;
                        border-radius: 32px;

                    }

                    span:nth-child(1) {
                        transform: translate(-50%, -50%) rotate(45deg);
                    }

                    span:nth-child(2) {
                        transform: translate(-50%, -50%) rotate(-45deg);
                    }
                }
            }

            .MenuTitle {
                width: 100%;
                height: 250px;
                display: flex;
                // border: 1px solid #000;

                .imgBox {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 250px;
                    height: 250px;

                    img {
                        // padding: 5%
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        border-radius: 12px;
                    }
                }

                .menuCategory {
                    width: 60%;
                    height: 100%;
                    display: flex;
                    flex-wrap: wrap;
                    // background-color: red;

                    .menuCategoryName {
                        width: 100%;
                        height: 20%;
                        padding: 5% 0 0 5%;
                        color: #ACACAC;
                        font-size: 2.5vw;
                        font-weight: bold;
                        // border: 1px solid #000;
                    }

                    .menuName {
                        width: 50%;
                        height: 50%;
                        padding-left: 7.5%;
                        font-size: 3.5vw;
                        padding-top: 25px;
                        text-align: center;
                        // border: 1px solid #000;
                    }

                    .menuCount {
                        width: 50%;
                        height: 50%;
                        padding-top: 25px;
                        padding-right: 0;
                        font-size: 40px;
                        // border: 1px solid #000;

                        div {
                            display: flex;
                            justify-content: end;
                            text-align: right;

                            button {
                                width: 50px;
                                height: 50px;
                                font-size: 40px;
                                background-color: #3F82C2;
                                color: #fff;
                                border: none;
                                border-radius: 4px;
                            }

                            p {
                                display: block;
                                width: 4.5vw;
                                font-size: 4vw;
                                text-align: center;
                            }
                        }
                    }

                    .menuPrice {
                        position: relative;
                        width: 100%;
                        height: 30%;
                        font-size: 3.5vw;
                        display: flex;

                        // border: 1px solid #000;
                        p {
                            width: 50%;
                            right: 0;
                            display: flex;
                            justify-content: end;
                            // border: 1px solid #000;
                        }

                        p:nth-child(1) {}

                        p:nth-child(2) {
                            color: #3F82C2;
                        }
                    }
                }
            }

        }

        .Center {
