                        .optionType {
                            margin-right: 10px;
                            color: #fff;
                            font-size: 24px;
                            font-weight: bold;
                            border: none;
                            background: none;

                        }

                        .icon {
                            width: 24px;
                            height: 24px;
                            // background-color: #000;
                            position: relative;

                            span {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                display: block;
                                width: 20px;
                                height: 5px;
                                border-radius: 4px;
                                background-color: #fff;
                            }

                            span:nth-child(1) {
                                transform: translate(-50%, -50%);
                            }

                            span:nth-child(2) {
                                transform: translate(-50%, -50%) rotate(90deg);
                            }

                        }
                    }
                }
            }

            // ========================================================
            // .options {
