            //         }


            //     }
            // }

            .optionItems {
                width: 100%;

                .option_item:last-child {
                    border-bottom: none;
                }

                .option_item {
                    padding-top: 1%;
                    border-bottom: 1px solid #313131;
                    display: flex;
