.Settings {
    width: 100%;
    height: 100%;
    * {
        box-sizing: border-box;
        font-size: 2.5vw;
        padding: 0;
        margin: 0;
    }

    .checkBox {
        width: 2.5vw;
        height: 2.5vw;
    }

    .radioButton {
        width: 2.5vw;
        height: 2.5vw;
    }

}

.SettingHeader {
    display: flex;
    width: 100%;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    border-bottom: none;
    // background-color: #000;

    a {
        border-bottom: 10px solid #000;
        width: 20%;
        height: 100px;
        /* 추가: 텍스트 중앙 정렬을 위해 */
        display: flex;
        /* 추가: Flexbox 모델 적용 */
        justify-content: center;
        /* 추가: 내용물을 가운데 정렬 */
        align-items: center;
        /* 추가: 내용물을 세로 중앙 정렬 */
        text-decoration: none;
        /* 추가: 링크 밑줄 제거 */
    }

    a:hover {
        background-color: #E1E1E1;
    }
}

.SettingBranch {
    .SettingBranchAdd {}

    .SettingBranchSelect {
        .seletedBranch {
            label {
                display: flex;

                input {
                    width: 5vw;
                    height: 5vw;
                }
                h2{
                    width: 20%;
                }
            
            }

            label:hover {
                cursor: pointer;
            }
        }
    }

    .SettingBranchMenuAdd {
        .branchCheck {
            label {
                display: flex;

                input {
                    width: 5vw;
                    height: 5vw;
                }
            }
            label:hover {
                cursor: pointer;
            }
        }

    }
}

