.SettingProductCreate {
    .productSettingLine {
        display: flex;
        align-items: stretch;

        div {
            border: 1px solid #000;
        }

        .productSettingName {
            width: 100%;
        }

        .productSettingValue {
            width: 80%;
        }
    }

    label {
        cursor: pointer;
    }

    .imageLabel {
        padding-top: 12px;
        padding-bottom: 12px;

        label {
            padding: 4px;
            background-color: #E1E1E1;
            border: 1px solid #000;
        }
    }
}

.SettingProductCreateOption {
    width: 100%;

    .optionHeader {
        h2 {
            display: flex;
            align-items: center;
            padding-left: 12px;
            font-size: 24px;
            background-color: #ccc;
            min-height: 50px;

        }

        .optionHeaders {
            display: flex;
            span {
                width: 25%;
                text-align: center;
                border: 1px solid #000;
            }
        }
    }
    .optionItem {
        display: flex;
        // padding: 4px;

        span{
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #000;
            width: 25%;
            height: 4vh;
        }


    }
}
