@charset "UTF-8";
.Kiosk {
  width: 1080px;
  height: 1920px;
  position: relative;
  /* Ensure this is set */
}

.test {
  width: 1080px;
  height: 1920px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}
.test img {
  position: absolute;
  z-index: 10;
  top: 0;
  width: 100%;
  height: 100%;
}

.TopBar {
  width: 100%;
  height: 3%;
  top: 0;
  bottom: 0;
}
.TopBar .topButton {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.TopBar .topButton .branchSetting {
  width: 10%;
  height: 100%;
  border: 1px solid #000;
  background-color: rgba(255, 255, 255, 0);
}
.TopBar .topButton .AIButton {
  width: 10%;
  height: 100%;
  border: 1px solid #000;
}
.TopBar .WebSocketVoiceStreaWming {
  width: 100%;
  height: 60px;
  border: 1px solid #000;
  background-color: red;
}

.Category {
  width: 100%;
  height: 15%;
}
.Category .menuCategory {
  width: 100%;
  height: 75%;
  display: flex;
  flex-wrap: wrap;
}
.Category .menuCategory .categoryBox {
  min-height: 40%;
  width: 20%;
  height: 50%;
  border-radius: 32px;
  margin: 4px;
  width: calc(20% - 8px);
  height: calc(50% - 8px);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  font-weight: bold;
  border: 1px solid #e1e1e1;
}
.Category .selectedCategory {
  background-color: #313131;
  color: #fff;
}
.Category .topLine {
  width: 100%;
  height: 25%;
  background-color: #313131;
}

.MenuList {
  width: 100%;
  height: 75%;
  display: flex;
  flex: wrap;
  align-items: flex-start;
  border-bottom: 1px solid #000;
}
.MenuList .menuListBox {
  position: relative;
  width: 25%;
}
.MenuList .menuListBox .menuImage {
  width: 100%;
}
.MenuList .menuListBox .menuImage img {
  width: 100%;
  height: 240px;
  object-fit: cover;
  padding: 5%;
  border-radius: 48px;
}
.MenuList .menuListBox .menuInfo {
  width: 100%;
  text-align: center;
}
.MenuList .menuListBox .menuInfo .menuName {
  font-size: 2.5vw;
  font-weight: 400;
}
.MenuList .menuListBox .menuInfo .menuPrice {
  padding-top: 10px;
  font-size: 2vw;
  font-weight: 400;
}
.MenuList .soldout::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  /* 반투명한 흰색 */
  z-index: 2;
  /* 내용 위에 오도록 설정 */
}

.OptionSection {
  color: #313131;
  position: absolute;
  top: 0;
  left: 0;
  width: 1080px;
  height: 1920px;
  background-color: rgba(0, 0, 0, 0.6);
  margin: 0 auto;
  z-index: 3;
}
.OptionSection .optionBox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  height: 1280px;
  padding-left: 50px;
  padding-right: 50px;
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
}
.OptionSection .optionBox .PersonalOption {
  width: 100%;
  height: 100%;
}
.OptionSection .optionBox .PersonalOption .personalTitle {
  margin-top: 50px;
  width: 100%;
  height: 60px;
  display: flex;
  font-size: 40px;
  color: #3F82C2;
}
.OptionSection .optionBox .PersonalOption .personalTitle div {
  z-index: 200;
  position: absolute;
  top: 10px;
  right: 15px;
  width: 60px;
  height: 60px;
  border-radius: 64px;
  background-color: #ACACAC;
}
.OptionSection .optionBox .PersonalOption .personalTitle div span {
  position: absolute;
  width: 50px;
  height: 8px;
  top: 50%;
  left: 50%;
  background-color: #fff;
  border-radius: 32px;
}
.OptionSection .optionBox .PersonalOption .personalTitle div span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}
.OptionSection .optionBox .PersonalOption .personalTitle div span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.OptionSection .optionBox .PersonalOption .personalOptions {
  padding-top: 20px;
  padding-left: 40px;
}
.OptionSection .optionBox .PersonalOption .personalOptions .personalOptionItems h1 {
  width: 100%;
  font-size: 32px;
}
.OptionSection .optionBox .PersonalOption .personalOptions .personalOptionItems .personalOptionItem {
  margin-top: 10px;
  display: flex;
}
.OptionSection .optionBox .PersonalOption .personalOptions .personalOptionItems .personalOptionItem .items div {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  background-color: rgba(231, 231, 231, 0.9058823529);
  width: 125px;
  height: 125px;
  border-radius: 12px;
  margin-right: 20px;
}
.OptionSection .optionBox .PersonalOption .personalOptions .personalOptionItems .personalOptionItem .items .pick {
  background-color: #3F82C2;
  color: #fff;
}
.OptionSection .optionBox .TOP {
  width: 100%;
  height: 330px;
}
.OptionSection .optionBox .TOP .exitButton {
  width: 100%;
  height: 80px;
  right: 0;
}
.OptionSection .optionBox .TOP .exitButton div {
  z-index: 100;
  position: absolute;
  top: 10px;
  right: 15px;
  width: 60px;
  height: 60px;
  border-radius: 64px;
  background-color: #ACACAC;
}
.OptionSection .optionBox .TOP .exitButton div span {
  position: absolute;
  width: 50px;
  height: 8px;
  top: 50%;
  left: 50%;
  background-color: #fff;
  border-radius: 32px;
}
.OptionSection .optionBox .TOP .exitButton div span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}
.OptionSection .optionBox .TOP .exitButton div span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.OptionSection .optionBox .TOP .MenuTitle {
  width: 100%;
  height: 250px;
  display: flex;
}
.OptionSection .optionBox .TOP .MenuTitle .imgBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 250px;
}
.OptionSection .optionBox .TOP .MenuTitle .imgBox img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory {
  width: 60%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuCategoryName {
  width: 100%;
  height: 20%;
  padding: 5% 0 0 5%;
  color: #ACACAC;
  font-size: 2.5vw;
  font-weight: bold;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuName {
  width: 50%;
  height: 50%;
  padding-left: 7.5%;
  font-size: 3.5vw;
  padding-top: 25px;
  text-align: center;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuCount {
  width: 50%;
  height: 50%;
  padding-top: 25px;
  padding-right: 0;
  font-size: 40px;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuCount div {
  display: flex;
  justify-content: end;
  text-align: right;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuCount div button {
  width: 50px;
  height: 50px;
  font-size: 40px;
  background-color: #3F82C2;
  color: #fff;
  border: none;
  border-radius: 4px;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuCount div p {
  display: block;
  width: 4.5vw;
  font-size: 4vw;
  text-align: center;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuPrice {
  position: relative;
  width: 100%;
  height: 30%;
  font-size: 3.5vw;
  display: flex;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuPrice p {
  width: 50%;
  right: 0;
  display: flex;
  justify-content: end;
}
.OptionSection .optionBox .TOP .MenuTitle .menuCategory .menuPrice p:nth-child(2) {
  color: #3F82C2;
}
.OptionSection .optionBox .Center {
  width: 100%;
  height: 700px;
  overflow: auto;
}
.OptionSection .optionBox .Center .basicOptions {
  padding-top: 40px;
  padding-left: 40px;
}
.OptionSection .optionBox .Center .basicOptions h1 {
  font-size: 28px;
  width: 100%;
}
.OptionSection .optionBox .Center .basicOptions .basicOption {
  display: flex;
}
.OptionSection .optionBox .Center .basicOptions .basicOption .option {
  margin-top: 10px;
  margin-right: 24px;
  width: 125px;
  height: 125px;
  border-radius: 12px;
}
.OptionSection .optionBox .Center .basicOptions .basicOption .option img {
  width: 100%;
  height: 100%;
  border-radius: 12px;
}
.OptionSection .optionBox .Center .optionShow {
  width: 100%;
  padding-top: 40px;
  padding-left: 40px;
}
.OptionSection .optionBox .Center .optionShow .name {
  width: 100%;
  font-size: 28px;
}
.OptionSection .optionBox .Center .optionShow article {
  display: flex;
  justify-content: space-between;
}
.OptionSection .optionBox .Center .optionShow article .price {
  color: #3F82C2;
  font-size: 24px;
}
.OptionSection .optionBox .Center .optionShow article .button {
  background-color: #3F82C2;
  padding: 5px 12px 5px 12px;
  border-radius: 4px;
  font-weight: bold;
  display: flex;
  position: relative;
}
.OptionSection .optionBox .Center .optionShow article .button .optionType {
  margin-right: 10px;
  color: #fff;
  font-size: 24px;
  font-weight: bold;
  border: none;
  background: none;
}
.OptionSection .optionBox .Center .optionShow article .button .icon {
  width: 24px;
  height: 24px;
  position: relative;
}
.OptionSection .optionBox .Center .optionShow article .button .icon span {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 20px;
  height: 5px;
  border-radius: 4px;
  background-color: #fff;
}
.OptionSection .optionBox .Center .optionShow article .button .icon span:nth-child(1) {
  transform: translate(-50%, -50%);
}
.OptionSection .optionBox .Center .optionShow article .button .icon span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(90deg);
}
.OptionSection .optionBox .Center .optionItems {
  width: 100%;
}
.OptionSection .optionBox .Center .optionItems .option_item:last-child {
  border-bottom: none;
}
.OptionSection .optionBox .Center .optionItems .option_item {
  padding-top: 1%;
  border-bottom: 1px solid #313131;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.OptionSection .optionBox .Center .optionItems .option_item .left {
  padding-left: 5%;
  display: flex;
  width: 90%;
}
.OptionSection .optionBox .Center .optionItems .option_item .left .arrowIcon {
  width: 7.5%;
  height: 10%;
}
.OptionSection .optionBox .Center .optionItems .option_item .left .arrowIcon img {
  width: 4vw;
  height: 4vw;
}
.OptionSection .optionBox .Center .optionItems .option_item .left .option_item_name {
  width: 42.5%;
  font-size: 2.5vw;
}
.OptionSection .optionBox .Center .optionItems .option_item .right {
  width: 10%;
  text-align: center;
}
.OptionSection .optionBox .Center .optionItems .option_item .right input {
  width: 5vw;
  height: 5vw;
}
.OptionSection .optionBox .Center .optionItems .option_item .option_item_detail {
  padding-left: 10%;
  width: 100%;
  display: flex;
}
.OptionSection .optionBox .Footer {
  width: 100%;
  height: 250px;
}
.OptionSection .optionBox .Footer .totalPrice {
  width: 100%;
  height: 50px;
  font-size: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 3px solid #000;
}
.OptionSection .optionBox .Footer .totalPrice p:nth-child(1) {
  padding-left: 40px;
}
.OptionSection .optionBox .Footer .totalPrice p:nth-child(2) {
  color: #3F82C2;
  font-weight: bold;
}
.OptionSection .optionBox .Footer .button {
  width: 100%;
  width: 700px;
  height: 200px;
  display: flex;
  align-items: center;
}
.OptionSection .optionBox .Footer .button button {
  border: none;
  font-size: 48px;
  font-weight: bold;
  border-radius: 8px;
}
.OptionSection .optionBox .Footer .button button:nth-child(1) {
  width: 250px;
  height: 150px;
  margin-right: 50px;
  background-color: #fff;
  color: #4169E1;
  border: 5px solid #4169E1;
}
.OptionSection .optionBox .Footer .button button:nth-child(2) {
  background-color: #4169E1;
  color: #fff;
  width: 400px;
  height: 150px;
}

.volume-gauge {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10px;
  background-color: #eee;
  border: 1px solid #ccc;
}

.bar {
  height: 100%;
  background-color: green;
}

